[mol_checker] {
	flex: 0 1 auto;
	justify-content: center;
	align-items: center;
	align-content: center;
	border: none;
	box-shadow: none;
	/*padding: .5rem 0;*/
	font-weight: normal;
}

[mol_checker]:before {
	content: 'âœ”';
	color: transparent;
	border-radius: var(--mol_skin_round);
	font-size: 1rem;
	width: 1.2rem;
	height: 1.2rem;
	flex: 0 0 1.2rem;
	display: flex;
	align-content: center;
	justify-content: center;
	align-items: center;
	opacity: .75;
	background: white;
	box-shadow: 0 0 0 1px var(--mol_skin_light_fore);
}

[mol_checker_checked]:before {
	background: var(--mol_skin_focus_back);
	color: var(--mol_skin_focus_fore);
	box-shadow: none;
}

[mol_checker]:not(:empty):before {
	margin-right: .5rem;
}
